@import url(https://fonts.googleapis.com/css?family=Grand+Hotel);
@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Grand+Hotel);
body {
  background-color: white;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.searchbar-container {
    border-bottom: 1px solid whitesmoke;
    border-left: 1px solid whitesmoke;
    border-right: 1px solid whitesmoke;
    display: flex;
    max-width: 650px;
    margin: 0 auto 30px auto;
    height: 60px;
    background-color: white;
    width: 100%;
  position: -webkit-sticky;
  position: sticky;
    right: 0;
    left: 0;
    margin-right: auto;
    margin-left: auto;
    top: -1px;
    
}
.far{
    cursor: pointer;
    display: flex;
    align-items: center;
    color: black;
    font-size: 25px;
}
.instagram-logo{
    margin: auto 0 auto 10px;
    cursor: pointer;
    width: 40px;
}

.divider{
    border-left: 1px solid black;
    height: 28px;
    margin: auto 0;
}

.instagram-letter-logo{
    padding-left: 1%;
    padding-right: 1%;
    cursor: pointer;
    font-family: 'Grand Hotel', cursive;
    font-size: 35px;
    width: 20%;
    margin: auto 0;}

.search-area{
    width: 60%;
    align-items: center;
    margin-top: 8px;
    margin: auto 0;
}

.search-input{
    height: 25px;;
    width: 65%;
    border-radius: 5px;
    background-color: aliceblue;
    text-align: center;
    margin: auto 0 auto 40px;
}


.rigth-container{
    display: flex;
    justify-content: flex-end;
    width: 25%;
}

.rigth-icon-container{
    display: flex;
    width: 100%;
    justify-content: space-around;
}

.post-container{
    font-family: 'Roboto', sans-serif;
    width: 85%;
    max-width: 600px;
    margin: 0 auto 30px auto;
    display: flex;
    flex-direction: column;
    border: 1px solid whitesmoke ;
    background-color: white;
    border-radius: 2px;
}

.header-container{
    font-size: 10px;
    height: 60px; 
    display: flex;
    padding-left: 3%;
    border-bottom: 1px solid whitesmoke;
}

.user-img{
    cursor: pointer;
    max-width: 650px;;
    margin: auto 0;
    border: 2px solid orange;
    border-radius: 90%;
    height: 30px;
}
.username{
    cursor: pointer;
    margin: auto 0 auto 2%;
    font-weight: bold;
}

.post-img{
    width: 100%;
}

.post-interaction{
    display: flex; 
    flex-direction: column;
    margin: 5px auto 0px auto;
    width: 95%;
}

.post-icons{
    max-width: 75px;
    width: 15%;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: 0;
}

.post-icons i {
    cursor: pointer;
    font-size: 25px;
    color: black;
    font-weight: lighter;
}

.post-icons i:nth-child(1) {
    margin-right: 2px;
}

.post-icons i:nth-child(2) {
    margin-right: 2px;
    color: red;
}

.hidden{
    display: none;
}

.likes{
    font-weight: 600;
    display: flex;
    justify-content: flex-start;
    width: 20%;
    margin: 5px 0 0 0;
    font-size: 15px;
}



.comments-main-container{
    width: 95%;
    margin: 5px auto 0 auto;
    
}
.comments-container{
    border-bottom: 1px solid lightgray;
}
.comment-container{
    display: flex; 
    text-align: start;
    font-family: 'Roboto', sans-serif;
}

.comment-username{
    margin: 0 5px 0 0;
    font-weight: bold;
}
.comment{
    margin: 0 0;
    color: gray;
}

.timestamp{
    margin-top: 5px;
    margin-bottom: 5px;
    text-align: start;
    color: darkgray;
    font-size: 13px;
}

.new-comment{
    display: flex;
    width: 100%;
}

.comment-input{
    max-width: 600px;
    font-size: 15px;
    color: gray;
    border: none;
    height: 50px;
    width: 90%;
    overflow: auto;
}

.comment-input.break {
    word-wrap: break-word;
    word-break: break-all;
    height: 80px;
}
.new-comment i{
    margin: auto 0 ;
    font-size: 20px;
    width: 10%;
}

.comment-username{
    font-weight:600;
    font-size: 15px;
}

.login-main-container{
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background:#EEEEEE;
    z-index: 10;
}

.login-container {
    border: 4px solid lightgray;
    border-radius: 4px;
    max-width: 300px;
    margin: 130px auto 0 auto;
    background-color: white;
    padding-bottom: 30px;
}
.login-header{
    text-align: center;
    font-family: 'Grand Hotel', cursive;
    font-size: 50px;
    font-weight: 100;
    margin-bottom: 10px;
}

.login-form {
    display: flex;
    flex-direction: column;
    width: 70%;
    margin: 0 auto;
}

.login-form input {
    margin-bottom: 5px;
    height: 30px;
    border-radius: 5px;
}

.login-form button{
    margin-top: 10px;
    background-color: #3897F0;
    color: white;
    height: 35px;
    border-radius: 5px;
}
